.contact {
  
  height: 88vh; /* note to self use a function here later to subtract 100vh from navbar vh */
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;

  &__container {
    /* media query 1200px and below width of 75% */
    height: 50rem;
    width: 66%;
    @include purpleGradient;
    box-shadow: 0 1.5rem 4rem rgba($color-black, .85);
  }

  &__card {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__text {
    font-size: 2.5rem;
    margin: 3rem;
  }

  &__link,
  &__link:active,
  &__link:visited {
    text-decoration: none;
    color: black;
    cursor: pointer;
  }

  &__text p {
    font-size: 1.7rem;
  }

  &__methods {
    font-size: 1.5rem;
    & p {
      font-weight: 700;
      text-decoration: none;
    }
  }

  &__icon {
    font-size: 7rem;
    margin-bottom: 1rem;
  }

  &__info {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1.5rem;
    /* margin: 0 2rem;
    padding: 10rem; */
  }
}

@media (max-width: 40.625em) {
  .contact {
    &__container {
      margin: 20rem 5rem 0 5rem;
      height: 72rem;
    }
    &__info {
      @include responsiveGrid;
    }
  }
}